import topBar from '../components/topBar.vue'
import articleList from '../components/articleList.vue'
export default {
  components: {
    topBar: topBar,
    articleList: articleList
  },
  data() {
    return {
      userInfo: {
        avatarUrl: '',
        nickName: '',
        mobile: '',
        sign: ''
      },
      mycircle: [{
        num: 0,
        name: 'focus_num',
        title: '关注'
      }, {
        num: 0,
        title: '粉丝',
        name: 'fans'
      }, {
        num: 0,
        title: '点赞',
        name: 'like_num'
      }],                               // 信息菜单栏
      articleList: '',                  // 分享文章列表
      like_article_list: [],            // 点赞文章列表
      tabList: ['分享', '点赞'],
      tabIndex: 0
    }
  },
  onLoad() {
    let that= this
    that.getDetail()
  },
  methods: {
    getDetail() {
      let that= this,
        params= {
          url: that.$api.userInfo
        }
      that.$httpRequest(params).then(res => {
        let response= res.data
        for(let item in that.userInfo) {
          that.userInfo[item]= response.userInfo[item]
        }
        console.log(response)
        that.articleList= response.article_list
        
        that.articleList= that.articleList.concat(response.article_list)
        
        that.like_article_list= response.like_article_list
        
        that.mycircle.map(item => {
          item.num= response.mycircle[item.name]
        })
      })
    },
    
    orderDetail() {
      uni.navigateTo({
        url: '../integral/integralDetail'
      })
    },
    goPersonal() {
      uni.navigateTo({
        url: './personal'
      })
    },
    goMember() {
      uni.navigateTo({
        url: './member'
      })
    },
    goSetting() {
      let user= this.userInfo
      uni.navigateTo({
        url: './setting?nickName=' + user.nickName + '&avatarUrl=' + user.avatarUrl
      })
    },
    selectTab(index) {
      this.tabIndex = index
    }
    
  }
}
